var after_base64 = '';
// 以下路径需根据项目实际情况填写
import {
	pathToBase64,
	base64ToPath
} from '../../convert.js';

export default {
	data() {
		return {
			resdata: '',
			token: '',
			fipth: '',
			tempFilePaths: '',
			checkingname: '',
			checkingdetail:'',
		}
	},
	onLoad() {
	},
	methods: {
		getfood(imgbase64) {
			var tk = '';
			console.log('resquesting...');
			uni.request({
				url: 'https://aip.baidubce.com/oauth/2.0/token?grant_type=client_credentials&client_id=nDnBr2Gy8om5m95BZhYKsCt3&client_secret=unZag4rfVkPbi7bGqckDsl3QMCl2ZG7m',
				header: {
					'Content-Type': 'Access-Control-Allow-Origin',
				},
				success: (res) => {
					// console.log(res.data);
					this.token = res.data.access_token;
					tk = res.data.access_token;
					console.log(tk);
					uni.request({
						url: 'https://aip.baidubce.com/rest/2.0/image-classify/v2/dish?access_token=' + tk, //仅为示例，并非真实接口地址。
						method: 'POST',
						header: {
							'content-type': 'application/x-www-form-urlencoded',
						}, //测试通过
						data: {
							'image': imgbase64,
						},
						//测试通过
						success: (res) => {
							this.checkingname = '名称：' + res.data.result[0].name;
							this.checkingdetail='热量：' + res.data.result[0].calorie + '卡路里 准确率：' + res.data
								.result[0].probability;
							//console.log(res.data.result[0].name);
						},
						fail: (res) => {
							this.checkingname ='未能识别！';
							this.checkingdetail='没有更多信息！';
						}
					});
				}
			});
		},
		chooseImage() {
			var _this = this;
			_this.checkingdetail='';
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择、摄像头
				success: function(res) {
					_this.tempFilePaths = res.tempFilePaths[0];
					_this.checkingname = '正在识别，请稍候。。。';
					//console.log(_this.tempFilePaths);
					pathToBase64(_this.tempFilePaths)
						.then(base64 => {
							after_base64 = base64.substring(22);
							_this.getfood(after_base64);
							//console.log(after_base64)
						})
						.catch(error => {
							console.error(error)
						})
				},
			});
		},
		// 获取图片信息  
